$white: #ffffff
$black: #000
$pink: #B1525E
$pink-dark: #B04A4A
$red: #B51B1B
$red-light: #C22727
$brown: #290B0B
$brown-light: #2F0A0A

// bp 1240, 950, 700, 520
@mixin media($bp-size)
  @media (max-width: $bp-size + px)
    @content

@mixin media_min($bp-size)
  @media (min-width: $bp-size + 0.1 + px)
    @content

@mixin hover
  @include media_min(1240)
    @content

