/*Обнуление*/
*
  margin: 0
  outline: none
  //outline: 1px solid crimson
  box-sizing: border-box
  padding: 0
  font-family: "Lato", sans-serif

body
  display: flex
  flex-direction: column
  min-width: 320px
  min-height: 100vh
  overflow-y: scroll
  //background-color: $white
  //color: $black

picture
  display: block

img
  display: block
  width: 100%
  height: auto

li
  list-style-type: none

a
  color: inherit
  cursor: pointer

button
  user-select: none
  cursor: pointer

/*--------------------*/
@font-face
  font-family: "PTMono"
  font-style: normal
  font-weight: 700
  font-display: block
  src: url('../assets/fonts/PTMono-Bold-01.ttf') format('ttf')

@font-face
  font-family: "PTMono"
  font-style: normal
  font-weight: 400
  font-display: block
  src: url('../assets/fonts/PTMono-Regular-02.ttf') format('ttf')

@font-face
  font-family: "GENTODREAM"
  font-style: normal
  font-weight: 400
  font-display: block
  src: url('../assets/fonts/GENTODREAM.ttf') format('ttf')

